import React, { useState} from "react"
import {createPost} from "../lookup"
import { PostList, TextAreaModel  } from "."




export function PostCompontent(props){
  const canPost = props.canPost === "false" ? false:true

  const imageInputRef = React.createRef()
  const textInputRef = React.createRef()
  const textAreaRef = React.createRef()
  const [newPosts, setNewPosts] = useState([])


  const handleCreateCallback = (response, status) =>{
    let tempNewPosts = [...newPosts]
    if(status===201){
      tempNewPosts.unshift(response)
      setNewPosts(tempNewPosts)
    }else{
      alert("An error occured please try again letter !")
    }
  }

const handleSubmit = (event) =>{
    event.preventDefault()
    const newVal = imageInputRef.current.files[0]
    const newContent = textAreaRef.current.value
    const newTitle = textInputRef.current.value

    let formdata = new FormData()
    formdata.append('title', newTitle)
    formdata.append('content', newContent)
    formdata.append('image', newVal)

    createPost(formdata, handleCreateCallback)


    textAreaRef.current.value = ""
    textInputRef.current.value = ""
    imageInputRef.current.value = null

  }
 

  return(
    <div>
      {canPost && <TextAreaModel />}
      <div className="modal fade" id="exampleModal" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">Post</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form onSubmit={handleSubmit}>
                    <div className="mb-3">
                      <label className="form-label">Title</label>
                      <input type="text" className="form-control" ref={textInputRef}   placeholder="title" />
                    </div>
                    <div className="mb-3">
                      <label className="form-label">Content </label>
                      <textarea className="form-control"  rows="3" ref={textAreaRef} ></textarea>
                    </div>

                    <div className="mb-3">
                      <label  className="form-label">Upload file</label>
                      <input className="form-control" type="file" ref={imageInputRef} />
                    </div>

                    <div className="modal-footer">
                      <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                      <button type="submit" className="btn btn-primary">Post</button>
                    </div>
                </form>  
                </div>
              </div>
            </div>
          </div>
        <PostList  newPosts={newPosts} {...props}/>
    </div>
  )
}









  

