import React, { useEffect, useState} from "react"
import { loadPost, loadFeed} from "../lookup"
import { Post } from "."


export function PostList(props){
    const {username} = props
    const [postsInits, setPostInits] = useState([])
    const [posts, setPosts] = useState([])
    const [nextUrl, setNextUrl] = useState(null)
  
    useEffect(()=>{
      let final = [...props.newPosts].concat(postsInits)
      if (final.length !== posts.length){
        setPosts(final)
      }
    }, [posts, props.newPosts, postsInits])
  
    useEffect(()=>{
      const myCallback = (response, status) =>{
        if(status === 200){
          setNextUrl(response.next)
          setPostInits(response.results)
          console.log(nextUrl)
        }
      }
      loadPost(username, myCallback)
    }, [username])
  
    const handleLoadbtn =(event)=>{
      event.preventDefault()
  
      const handleBackendLoad =(response, status)=>{
        if(status === 200){
          setNextUrl(response.next)
          const newPost = [...posts].concat(response.results)
          setPostInits(newPost)
          setPosts(newPost)
        }
      }
  
      loadPost(username, handleBackendLoad, nextUrl)
    }
  
  
    return (
      <div >
        <React.Fragment>
          {posts.map((post, index)=>{
            return <Post post={post}  key={`${index}`}/>
          })}
          <div className="d-grid gap-2  mx-auto mt-2">
            {nextUrl !== null && <button className="btn btn-outline-primary btn-sm mb-3 mx-auto" onClick={handleLoadbtn}>Load posts</button>}
          </div>
        </React.Fragment>
      </div>
    );
  }
  
 
export function FeedList(props){
    const [postsInits, setPostInits] = useState([])
    const [posts, setPosts] = useState([])
    const [didPost, setDidPost] = useState(false)
    const [nextUrl, setNextUrl] = useState(null)
  
    useEffect(()=>{
      let final = [...props.newPosts].concat(postsInits)
      if (final.length !== posts.length){
        setPosts(final)
      }
    }, [posts, props.newPosts, postsInits])
  
    useEffect(()=>{
      if(didPost === false){
         const myCallback = (response, status) =>{
        if(status === 200){
          setNextUrl(response.next)
          setPostInits(response.results)
          setDidPost(true)
        }
      }
      loadFeed(myCallback, nextUrl)
      
      }
      
    }, [ props.newPosts, posts, postsInits, nextUrl])
  
    const handleLoadbtn =(event)=>{
      event.preventDefault()
  
      if(nextUrl !== null){
        const handleBackendLoad =(response, status)=>{
        if(status === 200){
          setNextUrl(response.next)
          const newPost = [...posts].concat(response.results)
          setPostInits(newPost)
          setPosts(newPost)
        }
      }
      loadFeed(handleBackendLoad, nextUrl)
      }
     
      
    }
  
  
    return (
      <div className="mt-5">
        <React.Fragment>
          {posts.map((post, index)=>{
            return <Post post={post}  key={`${index}`}/>
          })}
          <div className="d-grid gap-2  mx-auto mt-2">
            {nextUrl !== null && <button className="btn btn-outline-primary btn-sm mb-3 mx-auto" onClick={handleLoadbtn}>Load posts</button>}
          </div>
        </React.Fragment>
        
      </div>
    );
  }
   
  
