import React, { useEffect, useState} from "react"
import { loadPostDetail } from "../lookup"
import { Post } from "."




export function PostCompontentDetail(props){
  const postId = props.postId
  const [post, setPost] = useState(null)
  const [didPost, setDidpost] = useState(false)

  const handleBackendPostDetail = (response, status) =>{
    if(didPost===false){
        if(status===200){
          setPost(response)
          setDidpost(true)
        }
    }
  }

  loadPostDetail(postId, handleBackendPostDetail)
  return post !== null ? <Post post={post} /> : null
}





  




  

