import React from "react"
import {  createACtionBtn } from "../lookup"

  export function ActionPostBtn(props){
    const {post, action, didLike} = props
    const likes = post.like ? post.like : 0

    const className = props.className ? props.className : "btn btn-primary btn-sm"
    const actionDisplay = action.display ? action.display : 'Action'
    const display = action.type === "like" ? `${likes} ${actionDisplay}` : actionDisplay

    const handleClick = (event) =>{
      event.preventDefault()

      const handleBackEndCallback = (response, status) =>{
        if((status === 200 || status === 201) && didLike){
          didLike(response)
        }
      }
      createACtionBtn({id:post.id, action:action.type}, handleBackEndCallback)
    }
    return(
        <button className={className} onClick={handleClick}>{display}</button>      
    )
  }







  

