import React, { useEffect, useState } from "react";
import "./Component.css";
import { apiCreateComment } from "../lookup";
import { UserDisplay, UserPicture, UserLink } from "../post";
import { ActionCommentBtn } from ".";

export function CommentComponent(props) {
const [showCommentLine, setShowCommentLine] = useState(false)
const [showButtons, setShowButtons] = useState(false)
const [enableBtn, setEnableBtn] = useState(true)

const commentFocus =()=>{
    setShowCommentLine(true)
    setShowButtons(true)
}

const commentFocusOut =()=>{
    setShowCommentLine(false)
}



  const { post } = props;
  const comment = React.createRef();

  const [newComment, setNewComment] = useState([]);

  const handleCommentBackend = (response, status) => {
    if (status === 201) {
      const tempNewComment = [...newComment];
      tempNewComment.unshift(response);
      setNewComment(tempNewComment);
    }
  };

  const commentStroke = event =>{
      const currentComment = event.target.value
      if (currentComment){
          setEnableBtn(false)
      }else{
          setEnableBtn(true)
      }
  }

  const handleCommentSubmit = (event) => {
    event.preventDefault();
    const newVal = comment.current.value;

    apiCreateComment({ post: post.id, content: newVal }, handleCommentBackend);
    comment.current.value = "";
   
    
   
  };

  return (
    <div>
      <form onSubmit={handleCommentSubmit}>
        <section className="commentBox">
          <textarea
            rows={2}
            className="mt-4"
            placeholder="Add a public comment"
            ref={comment}
            onFocus={commentFocus}
            onBlur={commentFocusOut}
            onKeyUp={commentStroke}

            
          ></textarea>
         {showCommentLine && <div className="mb-2 commentLine"></div>} 
          {/* <button>Comment</button> */}
        </section>
        {showButtons && (
            <>
            <button className="commentButton sendButton" disabled={enableBtn}>COMMENT</button>
            <button className="commentButton" style={{color:"gray", backgroundColor:"transparent"}}
                onClick={()=>{
                    setShowButtons(false)
                    comment.current.value=""
                }}
            >CANCEL</button>
            </>
        )}
      </form>

      <div>
        <CommentList post={post} newComment={newComment} />
      </div>
    </div>
  );
}

export function CommentList(props) {
  const { post } = props;
  const postInit = post.comments;
  const [arrowUp, setArroUp] = useState(false)
 

  const [posts, setPosts] = useState([]);
  const [countCom, setCountCom] = useState(posts.comment_count ? posts.comment_count : 0);

  let arrow = <i className="fas fa-caret-down"></i>

  const changeArrow =()=>{
      setArroUp(prevState =>prevState = !prevState)
  }

  if(arrowUp){
      arrow = <i className="fas fa-caret-up"></i>
  }else{
      arrow = <i className="fas fa-caret-down"></i>
  }

  useEffect(() => {
    let final = [...props.newComment].concat(postInit);
    if (final !== postInit) {
      setPosts(final);
      setCountCom(final.length)
    }
  }, [props.newComment, postInit]);

  return (
    <div>
    <section className="arrowComment" onClick={changeArrow}>
        {arrow}
        <div>View {countCom} Comments</div>
    </section>
    {arrowUp && (<section>
      {posts.map((comment, index) => {
        return <Comment comment={comment} key={`${index}`} />;
      })}
    </section>)}

      
    </div>
  );
}

function Comment(props) {
  const { comment } = props;
  const  {countCom} = props
  

  return (
    <div>
      <Messages comment={comment} countCom={countCom}/>
    </div>
  );
}

function Messages(props){
    const {comment} = props
    const [actionPost, setActionPost] = useState(props.comment ? props.comment : null)

    const handleDidActionLike = (newActionPost) =>{
      setActionPost(newActionPost)
    }
    return(
      <React.Fragment>
         <section className="messageContainer">
          <div>
            <div className="messageUser me-5" style={{marginLeft:"20px"}}>{comment.user.username}</div>
            <div className="username"><UserPicture user={comment.user}/></div>
           </div>
            <div className="messageText me-5" style={{marginLeft:"20px"}}>{comment.content}</div>
          

            <div className="col-12 col-md-12 col-sm-12 mx-auto">
                <div className="btn btn-group">
                {actionPost && <React.Fragment>
                  <ActionCommentBtn className="fas fa-thumbs-up me-4" comment={actionPost} didLike={handleDidActionLike}  action={{type:"like", display: "like"}}/>
                  <ActionCommentBtn className="fas fa-thumbs-down" comment={actionPost} didLike={handleDidActionLike}  action={{type:"unlike", display: "unlike"}}/>
                </React.Fragment>}

                </div>

      
              </div>

            {/* <section className="messageIconContainer" style={{marginLeft:"20px"}}>
                <i className="fas fa-thumbs-up"></i>
                <div>{comment.likes}</div>
                <i className="fas fa-thumbs-down"></i>
            </section> */}
        </section>
        </React.Fragment>
    )
}
