import React from "react"
import { createCommentACtionBtn } from "../lookup"


 export function ActionCommentBtn(props){
    const {comment, action, didLike} = props
    const likes = comment.likes ? comment.likes : 0

    let className = props.className ? props.className : "btn btn-primary btn-sm"
    const display = action.type === "like" ? ` ${likes} ` : null

    const handleClick = (event) =>{
      event.preventDefault()

      const handleBackEndCallback = (response, status) =>{
        if((status === 200 || status === 201) && didLike){
          didLike(response)
        }
      }
      createCommentACtionBtn({id:comment.id, action:action.type}, handleBackEndCallback)
    }
    return(
        <i className={className} onClick={handleClick}>{display}</i>      
    )
  }
